/* table_data.js
 * Where the main components of the table are filled in
*/
function Data (key, id, age, gender, race, living, income) {
    return {
      key: key,
      id: id,
      age: age,
      gender: gender,
      race: race,
      living: living,
      income: income
    }
}
/**
 * Randomize array element order in-place.
 * Using Durstenfeld shuffle algorithm.
 * Thanks: http://tiny.cc/rypd4y
 */
function shuffleArray(array) {
    return array[Math.floor(Math.random()*array.length)];
}
/* setRandomData {Function}
 * {param} size {Int} - Enter your amount of randomly generated sample data
 * let setOutput: http://tiny.cc/660b4y
 * let age: http://tiny.cc/hcqd4y
*/
function setRandomData (size) {
  var randomData = []
  for (let i = 0; i <= size; i++) {
    let id = Math.floor(1900 + i)
    let age = Math.floor(Math.random() * (35-18) + 20);
    let genderArray = ['Male', 'Female', 'Non-Binary']
    let raceArray = ['Caucasian', 'African American', 'Hispanic', 'NA'];
    let livingArray = ['Single', 'Married', 'Divorced', 'NA'];
    let incomeBase = Math.floor(Math.random() * (50000 - 10000) + 1500);
    let randomizedIncome = "$" + incomeBase.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    /*
     * {key} - where the participant is in our set
     * {id} - the randomly generated id given
     * {age} - age of participant range of [20-30]
     * {gender} - [m,f,na]
     * {race} - [w, b, h, na]
     * {living} - [single, married, divorce, na]
     * {income} - [0-Infinity]
    */
    randomData.push(
      Data(
        1+size[i], // key
        id, // id
        age, // age
        shuffleArray(genderArray), // gender
        shuffleArray(raceArray), // race
        shuffleArray(livingArray), // living
        randomizedIncome // income
      )
    )
  }
  return randomData;
}

export const data = setRandomData(6);
export const columns = [
  {title: 'ID',dataIndex: 'id', key: 'id',},
  {title: 'Age',dataIndex: 'age', key: 'age',},
  {title: 'Gender',dataIndex: 'gender',key: 'gender',},
  {title: 'Race',dataIndex: 'race',key: 'race',},
  {title: 'Living Situation',dataIndex: 'living',key: 'living',},
  {title: 'Household Income',dataIndex: 'income',key: 'income'}
]
